.search {
  &__label {
    padding: 10px; 
  }
  &__input {
      text-align: center;
    font-size: 16px;
    padding: 1px;
    box-shadow: 0 0 0.5px rgba(0, 178, 255, 1), 0 0 1px rgba(0, 178, 255, 1),
      0 0 2px rgba(0, 178, 255, 1), 0 0 4px rgba(38, 104, 127, 1),
      0 0 15px rgba(38, 104, 127, 1), 0 0 25px rgba(38, 104, 127, 1),
      0 0 50px rgba(38, 104, 127, 1);
      @media all and (min-width:768px){
    padding:4px;
    font-size: 20px;
  }
  @media all and (min-width:2000px){
    font-size: 30px;
    }
  }
 
}

